"use client";

import { useRouter } from "next/router";
import React from "react";

import { getMenu } from "@/features/header";

import { SHeader, SLogo, SMenu, SMenuItem, SMenuList } from "./header.styles";

export const Header = () => {
  const menuList = getMenu();
  const router = useRouter();

  return (
    <SHeader>
      <SMenu>
        <SLogo>Bean Scene</SLogo>
        <SMenuList>
          {menuList.map((menu) => {
            console.log(router.pathname);
            const isSelected = menu.path === router.pathname;

            return (
              <SMenuItem
                $selected={isSelected}
                key={menu.value}
                href={menu.path}
              >
                {menu.label}
              </SMenuItem>
            );
          })}
        </SMenuList>
        <div>f </div>
      </SMenu>
    </SHeader>
  );
};
